import './projects.scss';

import React from 'react';
import Link from 'gatsby-link'

export default class Projects extends React.Component {

  render(){
    let posts = this.props.data.allMarkdownRemark.edges;
    return (
      <div className="page projects container">
        {posts.map((p,i)=>(
          <div key={i} className="projects__item">
           <Link to={p.node.fields.slug}>
              <h1>{p.node.frontmatter.title}</h1>
              <p>
              {p.node.frontmatter.description}
              </p>
           </Link>
          </div>
        ))}
      </div>
    );
  }
}

export const pageQuery = graphql`
  query ProjectsIndexQuery {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { fileAbsolutePath: {regex : "\/projects/"} }
    ){
      edges {
        node {
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            description
          }
          fields {
            slug
          }
        }
      }
    }
  }`



